
// ncfxy-include
function includeFiles(){
	$('[ncfxy-include]').each(function(){
		var filename = $(this).attr('ncfxy-include');
		var target = $(this);
		$.get({
			url: filename,
			async: false		// 
		}).done(function(data){
			target.html(data);
		}).fail(function(data){
			target.html(data.responseText);
		});
	});
}
;
define("common", function(){});

